      <p class="bg-red-700/40 backdrop-blur-sm py-0 inline-flex px-4 ml-auto -skew-x-6">Preview</p>

      <img :src="imageurl" class="w-full h-40 -mt-6 object-cover sm:rounded-t newsimages" />

      <p class="text-xs mt-4 mb-2 opacity-80">{{ newsdate }}</p>
      <h2 class="sm:mb-3 text-base font-semibold newsheading">{{ newsheading }}</h2>
      <p class="newsdesc text-sm mt-5 max-h-20 pb-3 opacity-70 overflow-y-scroll">{{ newspara }}</p>
